import React, { Component } from 'react';
import { Grid, Cell} from 'react-mdl';
import Avatar from './avatarimg-ConvertImage.png';
import { BoxLoading } from "react-loadingg"
class Landing extends Component {
    constructor(props) {
        super(props);
        this.state = { isLoading: true }
    }

    componentDidMount() {
        this.setState({isLoading: false})
    }

  render() {

      return(
          this.state.isLoading ? <BoxLoading /> :
          <Grid className="landing_grid">
                <Cell className="landing-cell" col={10}>

                    <img src={Avatar} alt="avatar" className="landing_img" />

                    <div className="landing_banner">

                        <h1>Hi, I'm Caleb</h1>

                        <hr />

                        <p> Web Development | Data Science | Machine Learning</p>

                        <div className="landing_socials">
                            
                            <a href="https://www.linkedin.com/in/caleb-habtegebriel/" rel="noopener noreferrer"  target="_blank">
                                <i className="fa fa-linkedin-square"  aria-hidden="true" />
                            </a>
                            
                            <a href="https://github.com/CalebHabtegebriel" rel="noopener noreferrer"  target="_blank">
                                <i className="fa fa-github-square"  aria-hidden="true" />
                            </a>

                        </div>

                    </div>

                </Cell>
            </Grid>


      )
  }
}

export default Landing;